*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.margin-arriba {
  margin-top: 3rem;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

/** Media Queries **/
.dropup {
  position: relative;
  display: inline-block;
}
.dropup .dropbtn {
  cursor: pointer;
}

.dropup-content {
  display: none;
  position: absolute;
  background-color: #1e1f29;
  max-width: 40rem;
  z-index: 1;
}
.dropup-content a {
  color: #00b54f;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.formulario {
  padding: 2rem;
}
.formulario .campo {
  display: flex;
  flex-direction: column;
}
.formulario .campo label {
  color: #2f4858;
}
.formulario .campo input,
.formulario .campo select {
  height: 4rem;
  margin-bottom: 1rem;
}

.route {
  display: flex;
  justify-content: space-between;
}

.show {
  display: block;
}

.listado {
  display: flex;
  flex-direction: column;
}

.contenido-titulo h2 {
  font-size: 2.5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .contenido-titulo h2 {
    font-size: 5rem;
  }
}
.contenido-titulo h2 span {
  color: #00b54f;
}

.enca {
  text-align: center;
  color: #ffffff;
}

.contenedor-formulario {
  width: 50%;
  margin: 0 auto;
}
.contenedor-formulario .for-crear .campo {
  margin-top: 2rem;
}
.contenedor-formulario .for-crear label {
  color: #000;
}
.contenedor-formulario .for-crear .campo-check {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.contenedor-formulario .for-crear .boton {
  margin-top: 1rem;
  background-color: #00b54f;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .contenedor-formulario .for-crear .boton {
    width: auto;
  }
}
.contenedor-formulario .for-crear .boton:hover {
  background-color: #00e865;
  cursor: pointer;
}
.contenedor-formulario .campo-route {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.mensaje {
  margin-top: 5rem;
  text-align: center;
  padding: 2rem;
  background-color: #00b54f;
}
.mensaje h3 {
  color: #ffffff;
}

.cont-boton {
  width: 8%;
  margin: 0 auto;
  margin-top: 2rem;
}

.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin: 1rem 0;
  font-size: 1.2rem;
  max-width: 80rem;
}
.alerta.error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid #a90000;
  color: #a90000;
}
.alerta.exito {
  background-color: #e8fff2;
  border-left: 0.5rem solid #00b54f;
  color: #00b54f;
}

.contenedor-alerta {
  width: 50%;
}

.btn {
  background-color: #00b54f;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #00e865;
  cursor: pointer;
}

.barra-mobile {
  background-color: #00b54f;
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .barra-mobile {
    display: none;
  }
}
.barra-mobile h1 {
  color: #ffffff;
  margin: 0;
}
.barra-mobile .menu img {
  width: 0.8rem;
}
.barra-mobile .menu img:hover {
  cursor: pointer;
}

.barra {
  display: none;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #ffffff;
    padding: 1rem 2rem;
    box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
  }
}
.barra p {
  margin: 0;
  font-size: 2rem;
}
.barra p span {
  font-weight: 700;
  color: #00b54f;
}
.barra .cerrar-session {
  background-color: #004f22;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  font-size: 1.4rem;
  padding: 1rem;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .barra .cerrar-session {
    width: auto;
  }
}
.barra .cerrar-session:hover {
  background-color: #008239;
  cursor: pointer;
}

.dashboard {
  background-color: #f3f4f6;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
  overflow: scroll;
}
.dashboard .contenido {
  padding: 2rem;
}
.dashboard .contenido .nombre-pagina {
  font-size: 2.8rem;
  text-align: center;
}
.dashboard .formulario input:not([type=submit]) {
  background-color: #ffffff;
}
.dashboard .formulario input[type=submit] {
  background-color: #00b54f;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  padding: 1rem;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  .dashboard .formulario input[type=submit] {
    width: auto;
  }
}
.dashboard .formulario input[type=submit]:hover {
  background-color: #00e865;
  cursor: pointer;
}

.contenedor-nueva-tarea {
  display: flex;
  justify-content: center;
}

.sidebar {
  opacity: 0;
  background-color: #00b54f;
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 500ms;
}
.sidebar.mostrar {
  opacity: 1;
  padding: 2rem;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.9);
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
.sidebar.ocultar {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    height: 100%;
    opacity: 1;
    padding: 2rem;
    background-color: #00b54f;
  }
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .contenedor-sidebar h2 {
  color: #ffffff;
  margin: 0;
  text-align: center;
  font-size: 2.5rem;
}
.sidebar .contenedor-sidebar span {
  color: #2f4858;
}
.sidebar .contenedor-sidebar .cerrar-menu {
  display: flex;
  justify-content: center;
  height: 2rem;
  width: 2rem;
}
.sidebar .contenedor-sidebar .cerrar-menu img:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar .cerrar-menu {
    display: none;
  }
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  color: #ffffff;
  display: block;
  margin-bottom: 2rem;
  transition-property: background-color;
  transition-duration: 300ms;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 2.4rem;
  padding: 0.7rem;
  text-align: center;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: #004f22;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    font-size: 1.3rem;
    margin-bottom: 2rem;
    text-align: left;
  }
}
.sidebar .cerrar-sesion-mobile {
  margin-top: 10rem;
}
.sidebar .cerrar-sesion-mobile .cerrar-sesion {
  background-color: #00b54f;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .sidebar .cerrar-sesion-mobile .cerrar-sesion {
    width: auto;
  }
}
.sidebar .cerrar-sesion-mobile .cerrar-sesion:hover {
  background-color: #00e865;
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .cerrar-sesion-mobile {
    display: none;
  }
}

.modal-lt {
  -webkit-animation-name: animatetop;
  -webkit-animation-duration: 0.4s;
  animation-name: animatetop;
  animation-duration: 0.4s;
}
.modal-lt .formulario {
  max-width: 100rem;
  background-color: #ffffff;
  padding: 2rem;
  margin: 0 auto;
  transition-property: all;
  transition-timing-function: ease-in-out;
  transition-duration: 500ms;
  border-radius: 1rem;
}
.modal-lt .formulario legend {
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
  margin-bottom: 2rem;
  display: block;
}
.modal-lt .formulario input[type=text],
.modal-lt .formulario input[type=tel],
.modal-lt .formulario input[type=time],
.modal-lt .formulario input[type=number] {
  border: 1px solid #6b7280;
  border-radius: 0.5rem;
}
.modal-lt .formulario .opciones {
  display: flex;
  gap: 1rem;
  justify-content: space-between;
}
.modal-lt .formulario select {
  height: 4.14rem;
  border: 1px solid #6b7280;
  border-radius: 0.5rem;
}
.modal-lt .formulario .submit-nueva-cancha {
  background-color: #00b54f;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .modal-lt .formulario .submit-nueva-cancha {
    width: auto;
  }
}
.modal-lt .formulario .submit-nueva-cancha:hover {
  background-color: #00e865;
  cursor: pointer;
}
.modal-lt .formulario .cerrar-modal {
  background-color: #f67b00;
  padding: 1.5rem 4rem;
  color: #ffffff;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .modal-lt .formulario .cerrar-modal {
    width: auto;
  }
}
.modal-lt .formulario .cerrar-modal:hover {
  background-color: #ff952a;
  cursor: pointer;
}
.modal-lt .formulario.animar {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal-lt .formulario.cerrar {
  transform: translateY(-200%);
  opacity: 0;
}
.modal-lt .formulario .horario {
  display: flex;
  justify-content: space-around;
}
.modal-lt .formulario .contenedor-dos {
  display: flex;
  gap: 2rem;
}
.modal-lt .formulario .contenedor-dos .input-todo {
  width: 100%;
}

@-webkit-keyframes animatetop {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}
@keyframes animatetop {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}
.contenedor-form {
  display: flex;
  gap: 2rem;
}
.contenido-cancha {
  display: flex;
  justify-content: space-between;
  gap: 2rem;
}
.contenido-cancha .imagen-config {
  width: 50%;
}
.contenido-cancha .cont-borde {
  background-color: white;
  padding: 2rem;
  border-radius: 2rem;
}
.contenido-cancha .contenido-detalle a {
  font-size: 1.5rem;
}
.contenido-cancha .contenido-detalle h4 {
  font-size: 3rem;
}

.acciones {
  display: flex;
  justify-content: space-between;
}

/***  IMAGEN *****/
#myImg {
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s;
}

#myImg:hover {
  opacity: 0.7;
}

/* The Modal (background) */
.modal {
  display: none;
  /* Hidden by default */
  position: fixed;
  /* Stay in place */
  z-index: 1;
  /* Sit on top */
  padding-top: 100px;
  /* Location of the box */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: black;
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.9);
  /* Black w/ opacity */
}

/* Modal Content (image) */
.modal-content {
  margin: auto;
  display: block;
  width: 80%;
  max-width: 700px;
}

/* Caption of Modal Image */
#caption {
  margin: auto;
  display: block;
  width: 80%;
  max-width: 700px;
  text-align: center;
  color: #ccc;
  padding: 10px 0;
  height: 150px;
}

/* Add Animation */
.modal-content, #caption {
  -webkit-animation-name: zoom;
  -webkit-animation-duration: 0.6s;
  animation-name: zoom;
  animation-duration: 0.6s;
}

@-webkit-keyframes zoom {
  from {
    -webkit-transform: scale(0);
  }
  to {
    -webkit-transform: scale(1);
  }
}
@keyframes zoom {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
/* The Close Button */
.close {
  position: absolute;
  top: 15px;
  right: 35px;
  color: #f1f1f1;
  font-size: 40px;
  font-weight: bold;
  transition: 0.3s;
}

.close:hover,
.close:focus {
  color: #bbb;
  text-decoration: none;
  cursor: pointer;
}

/* 100% Image Width on Smaller Screens */
@media only screen and (max-width: 700px) {
  .modal-content {
    width: 100%;
  }
}
